.result {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    margin: 0 0.5rem 3rem 0.5rem;
    color: #eeeeee;
    &-definition {
        & h2 {
            font-size: 1.2em;
            font-family: 'Poppins', sans-serif;
            margin-bottom: 0.5rem;
        }
        & p {
            font-size: 1em;
            font-family: 'Work Sans', sans-serif;
            font-weight: 300;
            margin-bottom: 0.5rem;
        }
    }
}